#demo {
	section {
		margin: 60px auto; } }

body {
	font-family: arial; }

.navigation {
	display: flex;
	background: rgba(0,0,0,0.65);
	align-items: center;
	color: white;
	position: fixed;
	width: 100%;
	margin-top: 0px;
	padding: 0.5%;
	z-index: 10;
	.logo {
		margin-left: 2%;
		width: 150px;
		a {
			text-decoration: none;
			color: white;
			font-size: 22px; } }

	.nav {
		margin: 0;
		justify-content: flex-end; } }

ul.nav {
	display: flex;
	position: absolute;
	right: 0px;
	li {
		list-style-type: none;
		margin: 0% 1%;
		a {
			text-decoration: none;
			color: white;
			margin-right: 50px; }
		a:visited {
			text-decoration: underline; } } }

ul.nav li:hover {
	text-decoration: underline; }

.header {
	height: 50vh;
	background-image: url('images/fpoHeader.jpg');
	background-position:: 5px 785px;
	background-size: cover;
	height: 600px;
	position: relative;
	h1 {
		font-size: 100px;
		color: rgba(255, 255, 255, 0.2);
		font-weight: lighter;
		margin-top: 190px;
		margin-bottom: 0px; }
	h2 {
		margin: 0 auto;
		width: 50%;
		font-size: 60px;
		color: #fff;
		font-weight: lighter; } }

.inner {
	text-align: center;
	padding-top: 45px;
	position: absolute;
	background: rgba(69,40,130,0.60);
	top: 0;
	bottom: 0;
	left: 0;
	right: 0; }

h2.indexH1 {
	font-size: 45px;
	margin-top: 150px; }

p.indexP {
	width: 76%;
	color: white; }

.about {
	position: relative;
	color: #5a5a5a;
	display: flex;
	line-height: 28px;
	margin-top: 3%;
	font-size: 16px;
	.leftCollumn {
		padding: 2%;
		width: 50%; }
	.rightCollumn {
		padding: 2%;
		width: 50%; } }

.about:before {
	content: '';
	width: 95%;
	border-bottom: solid 1px #8A8A8A;
	position: absolute;
	left: 23px;
	top: 18px;
	z-index: 1; }

.strategies {
	position: relative;
	margin-top: 5%;
	line-height: 28px;
	a {
		text-decoration: none;
		color: white;
		height: 25px;
		border: 1px solid white;
		padding: 10px;
		border-radius: 50px; } }

.boxes {
	justify-content: center;
	display: flex;
	width: 100%;
	text-align: center;
	margin-top: 2.5%;
	margin-bottom: 5%; }



.title {
	font-size: 22px;
	font-weight: bold;
	line-height: 30px;
	width: 35%;
	margin: 0 auto;
	text-align: center; }

.strategies:before {
	content: '';
	width: 100%;
	border-bottom: solid 5px #452882;
	position: absolute;
	top: -12.5px;
	z-index: 1; }

