// 	:content ''
// 	:width 100%
// 	:border-bottom solid 5px #452882
// 	:position absolute
// 	:top 70px
// 	:z-index 1

.full-width-box {
	width: 100%;
	position: absolute;
	z-index: 0; }

.box1 {
	background-image: url('images/seval.jpg');
	background-position: -25px;
	background-size: cover;
	width: 32%;
	margin: 1%;
	height: 350px;
	border-top-left-radius: 5%;
	border-top-right-radius: 5%;
	position: relative;
	overflow: hidden; }

.box2 {
	background-image: url('images/sinter.jpg');
	background-position: 0px -55px;
	background-size: cover;
	width: 32%;
	margin: 1%;
	height: 350px;
	border-top-left-radius: 5%;
	border-top-right-radius: 5%;
	position: relative;
	overflow: hidden; }

.box3 {
	background-image: url('images/sped.jpg');
	background-size: cover;
	width: 32%;
	margin: 1%;
	height: 350px;
	border-top-left-radius: 5%;
	border-top-right-radius: 5%;
	position: relative;
	overflow: hidden; }

.hoverEffect {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	transform: translate(0px,200px);
	background: rgba(69,40,130,0.85);
	z-index: 1;
	border-radius: 0%;
	color: white;
	transition: all 0.5s linear; }



.hoverEffect p {
	margin: 0 auto;
	width: 75%;
	margin-top: 50px;
	margin-bottom: 50px;
	font-size: 28px;
	line-height: 33px; }

.hoverEffect button {
	background: none;
	color: white;
	border: 1px solid white;
	padding: 3%; }

.boxes {
	>div:hover .move-up {
		transform: translate(0,0px);
		-webkit-transform: translate(0,0px);
		-o-transform: translate(0,0px);
		-moz-transform: translate(0,0px); } }

.footer {
	background: black;
	color: white;
	display: flex;
	padding: 2%;
	position: relative; }

.footer i {
	font-size: 2rem;
	width: 50px;
	height: 50px;
	line-height: 50px;
	text-align: center;
	border-radius: 50%;
	margin: 0 5px;
	border: 1px solid white; }

.footer i.fa.fa-instagram {
 }	// :padding 12%

.footer i.fa.fa-facebook {
	// :padding 16%
 }	// :font-size 1.95em

.footer .logo p {
	width: 150px; }

.footer ul.socialMedia {
	position: absolute;
	right: 5%; }

.footer ul li {
	list-style-type: none; }

.footer ul li a {
	text-decoration: none;
	color: white; }

.socialMedia {
	display: flex; }


//EVALUATION STYLING//

.pageh1 {
	margin-left: 42px; }


.pageh2 {
	font-size: 63px; }

.header-evaluation {
	height: 440px;
	background-image: url('images/evaluationHeader.jpg'); }

.header-intervention {
	height: 440px;
	background-image: url('images/interventionHeader.jpg');
	background-position:: 5px 660px; }

.header-pedagogy {
	height: 440px;
	background-image: url('images/pedagogyHeader.jpg');
	background-position:: 5px 660px; }


.strategy_description {
	margin-left: 170px; }

.inner-evaluation {
	color: white;
	text-align: left; }

.inner-evaluation h1.pageh1 {
	color: rgba(255,255,255,0.25);
	margin-top: 70px; }

.inner-evaluation h2.pageh2 {
	margin-bottom: -20px;
	margin-left: 170px;
	margin-top: 195px;
	width: 75%; }


p.strategy_description {
	width: 76%;
	font-size: 30px;
	line-height: 30px; }

.videos {
	margin-top: 80px; }

.video {
	display: flex;
	justify-content: center;
	box-shadow: 3px 3px 2px rgba(212,212,212,1);
	padding: 2.5%;
	margin: 30px;
	padding-top: 0%; }

.video:hover {
	// :box-shadow 8px 8px 4px rgba(212,212,212,1)
	background: rgba(227, 227, 227, 0.45);
	transition: background .5s ease-out;
	moz-transition: background .5s ease-out;
	webkit-transition: background .5s ease-out;
	o-transition: background .5s ease-out; }

a.video {
	display: flex;
	justify-content: center;
	box-shadow: 3px 3px 2px rgba(212,212,212,1);
	padding: 2.5%;
	margin: 30px;
	padding-top: 0%; }

.video_left {
	width: 30%;
	padding-left: 30px;
	padding-top: 25px;
	img {
		width: 250px; } }

.video_right {
	width: 60%;
	.video_title {
		font-weight: bold;
		font-size: 1.5em;
		margin-bottom: 0px;
		width: 75%; }
	.video_description {
		line-height: 22px;
		width: 90%; } }

//SLIDER

.flickity-viewport {
	height: 400px; }

.flickity-prev-next-button .arrow {
	fill: white !important; }

.flickity-prev-next-button {
	background: hsla(0,0%,0%,0) !important; }

.carousel-cell {
	width: 100%;
	height: 300px; }

.inner-cell {
	display: flex;
	.cell-left {
		width: 70%; }
	.cell-right {
		width: 30%;
		img {
			height: 260px;
			transform: rotate(3deg);
			box-shadow: 3px 3px 50px;
			margin-top: 20px; } } }

.cellHeader {
	font-size: 24px;
	font-weight: bold;
	width: 70%;
	margin: 0 auto;
	text-align: center;
	line-height: 30px;
	margin-top: 45px; }

.cellSub {
	margin: 0 auto;
	width: 50%;
	text-align: center;
	margin-top: 15px;
	font-size: 20px; }

.cellButton {
	background: #FF7E60;
	text-decoration: none;
	color: white;
	font-size: 15px;
	padding: 20px;
	margin: 0 auto;
	display: block;
	width: 50%;
	text-align: center;
	margin-top: 20px;
	box-shadow: 1px 1px 5px black; }


.cellOne {
	background: #89CBE0; }

.cellTwo {
	background: #9197E5;
	.cellButton {
		background: black; } }

.cellThree {
	background: #7BCA68;
	.cellButton {
		background: #186340; } }

//VIDEO SLIDER

.fancybox-content {
	position: absolute !important;
	left: 0 !important; }

.fancybox-caption-wrap {
	top: 0 !important;
	width: 20% !important;
	right: 0 !important;
	left: auto !important; }

a.video p {
	text-decoration: none;
	color: black; }

a.video {
	text-decoration: none; }

span {
	font-size: 30px;
	line-height: 35px; }


//KEYFRAMES

@keyframes aslide {
	0% {
		border-radius: 0%; }
	100% {
		border-radius: 5%; } }

@keyframes slider {
	0% {
		border-radius: 0%; }
	100% {
		border-radius: 5%; } }


.move-up:hover {
	animation: slider 0.5s linear;
	animation: aslide 0.5s linear; }

.allbox:hover {
	animation: aslide 0.5s linear; }

