//MediaQueries
@media (max-width:900px) {
	.header {
		height: 450px;
		h1 {
			font-size: 70px;
			margin-top: 70px; }
		h2 {
			font-size: 50px;
			width: 90%; } }

	p.indexP {
		width: 100%;
		margin: 0 auto;
		margin-top: 15px; }

	.title {
		width: 80%; }

	.video_right {
		padding-left: 15%; }

	.cellHeader {
		font-size: 20px; }

	.cellSub {
		font-size: 15px; }

	.inner-cell .cell-right img {
		height: 220px;
		margin-top: 40px; }

	.fancybox-content {
		width: 70% !important; }

	.fancybox-caption-wrap {
		width: 30% !important; } }

@media (max-width:675px) {
	.header {
		height: 460px;
		h1 {
			font-size: 60px;
			margin-top: 70px; }
		h2 {
			font-size: 40px;
			width: 90%; } }

	.inner-evaluation h2.pageh2 {
		margin-top: 215px;
		margin-left: 85px;
		font-size: 40px; }

	p.strategy_description {
		width: 65%;
		font-size: 15px;
		line-height: 18px;
		margin-top: 20px;
		margin-left: 85px; }

	.about {
		display: block;
		font-size: 14px;
		.leftCollumn {
			width: 100%; }
		.rightCollumn {
			width: 100%; } }

	.about:before {
		width: 100%;
		left: 0; }


	.boxes {
		display: block; }

	.allbox {
		width: 65%;
		margin: 0 auto;
		margin-top: 40px; }

	a.video {
		display: block;
		.video_left {
			width: 100%;
			text-align: center; }
		.video_right {
			width: 100%;
			.video_title {
				font-size: 20px;
				width: 100%; }
			.video_description {
				display: none;
				// :font-size 17px
 } } }				// :width 100%

	.cellHeader {
		line-height: 20px; }
	.cellButton {
		width: 70%; }

	.fancybox-content {
		display: inline-block;
		position: relative;
		margin: 0 auto;
		padding: 0;
		width: 100% !important;
		height: calc(100% - 88px);
		vertical-align: middle;
		line-height: normal;
		text-align: center; }

	.fancybox-caption-wrap {
		position: absolute;
		bottom: 0 !important;
		left: 0 !important;
		right: 0 !important;
		padding: 60px 30px 0 30px;
		z-index: 99998;
		width: 100% !important;
		backface-visibility: hidden;
		box-sizing: border-box;
		background: linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.1) 20%, rgba(0, 0, 0, 0.2) 40%, rgba(0, 0, 0, 0.6) 80%, rgba(0, 0, 0, 0.8) 100%);
		opacity: 1;
		transition: opacity .2s;
		pointer-events: none;
		margin-top: 90%; } }



@media (max-width:500px) {
	ul.nav li {
		margin-left: 3%;
		a {
			margin-right: 0px; } }

	.navigation .logo a {
		font-size: 18px; }

	.header {
		h2 {
			font-size: 30px; } }

	.inner-evaluation h1.pageh1 {
		margin-top: 45px;
		margin-left: 20px; }

	// .inner-evaluation h2.pageh2
	// 	:margin-left 20px

	p.strategy_description {
		margin-left: 90px; }

	p.indexP {
		margin-left: 0 auto; }

	.title {
		width: 100%;
		font-size: 20px; }

	.allbox {
		width: 75%; }

	.cellHeader {
		font-size: 20px; }

	.cellSub {
		display: none; }

	.inner-cell .cell-right img {
		height: 140px;
		margin-top: 85px; }

	.cellButton {
		font-size: 15px;
		padding: 10px; } }


